import { OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { PronoDialogComponent } from './../prono-dialog/prono-dialog.component';
import { AuthService } from '../../../auth.service';
import { environment } from '../../../../environments/environment';
var PronoFormComponent = /** @class */ (function () {
    function PronoFormComponent(http, formBuilder, router, dialog, auth) {
        this.http = http;
        this.formBuilder = formBuilder;
        this.router = router;
        this.dialog = dialog;
        this.auth = auth;
        this.title = 'app';
        this.disable = false;
        this.disableM1 = false;
        this.disableM2 = false;
        this.disableM3 = false;
        this.disableM4 = false;
        this.disableM5 = false;
        this.disableM6 = false;
        this.disableM7 = false;
        this.disableM8 = false;
        this.disableM9 = false;
        this.disableM10 = false;
        this.showWarningMessage = false;
        this.errorFlag = false;
    }
    PronoFormComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.form = this.formBuilder.group({
            m1: new FormControl('', Validators.required),
            m2: new FormControl('', Validators.required),
            m3: new FormControl('', Validators.required),
            m4: new FormControl('', Validators.required),
            m5: new FormControl('', Validators.required),
            m6: new FormControl('', Validators.required),
            m7: new FormControl('', Validators.required),
            m8: new FormControl('', Validators.required),
            m9: new FormControl('', Validators.required),
            m10: new FormControl('', Validators.required),
            pseudo: new FormControl('', Validators.required),
            matchday: 0
        });
        this.http.get(environment.apiUrl.concat('fixtures/current/'))
            .toPromise().then(function (data) {
            //this.matchday = Number(data);
            _this.matchday = 38;
            _this.form.controls['matchday'].setValue(_this.matchday);
            if (_this.auth.isLoggednIn()) {
                var pseudo = _this.auth.getDecodeToken();
                _this.disable = true;
                _this.form.controls['pseudo'].setValue(pseudo);
                //get prono if already done
                _this.http.get(environment.apiUrl.concat('pronostic/get/').concat(_this.matchday).concat('&').concat(pseudo))
                    .toPromise().then(function (data) {
                    if (data != null) {
                        _this.form.controls['m1'].setValue(data.m1);
                        _this.form.controls['m2'].setValue(data.m2);
                        _this.form.controls['m3'].setValue(data.m3);
                        _this.form.controls['m4'].setValue(data.m4);
                        _this.form.controls['m5'].setValue(data.m5);
                        _this.form.controls['m6'].setValue(data.m6);
                        _this.form.controls['m7'].setValue(data.m7);
                        _this.form.controls['m8'].setValue(data.m8);
                        _this.form.controls['m9'].setValue(data.m9);
                        _this.form.controls['m10'].setValue(data.m10);
                    }
                }, function (error) {
                    //pas de prono enregistré donc on fais rien
                });
            }
            else {
                _this.form.controls['pseudo'].enable();
            }
        });
        this.http.get("./assets/json/l1.json").toPromise().then(function (data) {
            var teams = {};
            for (var index in data) {
                var jsonTeam = data[index];
                teams[jsonTeam['name']] = jsonTeam['logo'];
            }
            _this.http.get(environment.apiUrl.concat('fixtures/38'))
                .toPromise().then(function (data) {
                // Read the result field from the JSON response.
                _this.games = [];
                for (var i = 1; i < 11; i++) {
                    var fixture = data[i - 1];
                    var game = {
                        dom: fixture['home'],
                        logoDom: teams[fixture['home']],
                        ext: fixture['away'],
                        logoExt: teams[fixture['away']],
                        id: i,
                        result: '',
                        rankingDom: fixture['rankingHome'],
                        rankingExt: fixture['rankingAway'],
                        resultHomeTeamJ1: fixture['previousResultHome'][0],
                        resultAwayTeamJ1: fixture['previousResultAway'][0],
                        resultHomeTeamJ2: fixture['previousResultHome'][1],
                        resultAwayTeamJ2: fixture['previousResultAway'][1],
                        resultHomeTeamJ3: fixture['previousResultHome'][2],
                        resultAwayTeamJ3: fixture['previousResultAway'][2],
                        resultHomeTeamJ4: fixture['previousResultHome'][3],
                        resultAwayTeamJ4: fixture['previousResultAway'][3],
                        resultHomeTeamJ5: fixture['previousResultHome'][4],
                        resultAwayTeamJ5: fixture['previousResultAway'][4],
                    };
                    _this.games.push(game);
                }
            });
        });
        this.http.get(environment.apiUrl.concat('matchday/38'))
            .toPromise().then(function (data) {
            var today = Date.now();
            if (today >= data['m1Date']) {
                _this.disableM1 = true;
                _this.form.controls['m1'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m2Date']) {
                _this.disableM2 = true;
                _this.form.controls['m2'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m3Date']) {
                _this.disableM3 = true;
                _this.form.controls['m3'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m4Date']) {
                _this.disableM4 = true;
                _this.form.controls['m4'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m5Date']) {
                _this.disableM5 = true;
                _this.form.controls['m5'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m6Date']) {
                _this.disableM6 = true;
                _this.form.controls['m6'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m7Date']) {
                _this.disableM7 = true;
                _this.form.controls['m7'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m8Date']) {
                _this.disableM8 = true;
                _this.form.controls['m8'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m9Date']) {
                _this.disableM9 = true;
                _this.form.controls['m9'].setValidators(null);
                _this.showWarningMessage = true;
            }
            if (today >= data['m10Date']) {
                _this.disableM10 = true;
                _this.form.controls['m10'].setValidators(null);
                _this.showWarningMessage = true;
            }
        });
    };
    Object.defineProperty(PronoFormComponent.prototype, "pseudo", {
        get: function () { return this.form.get('pseudo'); },
        enumerable: true,
        configurable: true
    });
    PronoFormComponent.prototype.getClassForResult = function (value) {
        if (value == 'V') {
            return 'badge badge-pill badge-success';
        }
        else if (value == 'N') {
            return 'badge badge-pill badge-warning';
        }
        else {
            return 'badge badge-pill badge-danger';
        }
    };
    PronoFormComponent.prototype.saveProno = function () {
        var _this = this;
        this.form.setValue;
        if (this.auth.isLoggednIn()) {
            this.http.post(environment.apiUrl.concat('pronostic/true'), this.form.value)
                .toPromise().then(function (d) {
                _this.router.navigate(['/pronostics']);
                _this.openDialog();
            });
        }
        else {
            this.http.post(environment.apiUrl.concat('pronostic/false'), this.form.value)
                .toPromise().then(function (d) {
                _this.router.navigate(['/pronostics']);
                _this.openDialog();
            }, function (error) {
                if (error.status == 401) {
                    _this.errorFlag = true;
                }
            });
        }
    };
    PronoFormComponent.prototype.isDisable = function (id) {
        var disable = false;
        switch (id) {
            case 1:
                disable = this.disableM1;
                break;
            case 2:
                disable = this.disableM2;
                break;
            case 3:
                disable = this.disableM3;
                break;
            case 4:
                disable = this.disableM4;
                break;
            case 5:
                disable = this.disableM5;
                break;
            case 6:
                disable = this.disableM6;
                break;
            case 7:
                disable = this.disableM7;
                break;
            case 8:
                disable = this.disableM8;
                break;
            case 9:
                disable = this.disableM9;
                break;
            case 10:
                disable = this.disableM10;
                break;
        }
        return disable ? '' : null;
    };
    PronoFormComponent.prototype.openDialog = function () {
        var dialogRef = this.dialog.open(PronoDialogComponent, {
            width: '250px'
        });
    };
    return PronoFormComponent;
}());
export { PronoFormComponent };
