import { OnInit } from '@angular/core';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { Team } from '../../../models/team';
import { Match } from '../../../models/match';
import { Router } from '@angular/router';
var PronoCdmKnockoutComponent = /** @class */ (function () {
    function PronoCdmKnockoutComponent(fb, http, router) {
        this.fb = fb;
        this.http = http;
        this.router = router;
        this.current_knockout = 'round_16';
        this.form = this.fb.group({
            m1: new FormControl('', Validators.required),
            m2: new FormControl('', Validators.required),
            m3: new FormControl('', Validators.required),
            m4: new FormControl('', Validators.required),
            m5: new FormControl('', Validators.required),
            m6: new FormControl('', Validators.required),
            m7: new FormControl('', Validators.required),
            m8: new FormControl('', Validators.required),
            pseudo: new FormControl('', Validators.required),
            knockout: this.current_knockout
        });
    }
    PronoCdmKnockoutComponent.prototype.ngOnInit = function () {
        var _this = this;
        this.initValidators();
        this.http.get("./assets/json/cdm.json").toPromise().then(function (data) {
            var jsonTeams = data['teams'];
            _this.teams = [];
            _this.matchs = [];
            for (var index in jsonTeams) {
                var jsonTeam = jsonTeams[index];
                var team = new Team();
                team.id = jsonTeam['id'];
                team.name = jsonTeam['name'];
                team.flag = jsonTeam['iso2'];
                _this.teams.push(team);
            }
            var jsonknockout = data['knockout'];
            for (var round in jsonknockout) {
                if (round == _this.current_knockout) {
                    var jsonRound = jsonknockout[round];
                    var name_1 = jsonRound['name'];
                    var jsonMatchs = jsonRound['matches'];
                    var idMatch = 1;
                    for (var index in jsonMatchs) {
                        var jsonMatch = jsonMatchs[index];
                        var match = new Match();
                        //match.id = jsonMatch['name'];
                        match.id = idMatch;
                        idMatch++;
                        //match.home = this.teams[jsonMatch['home_team'] - 1];
                        var home_team = new Team();
                        home_team.id = 0;
                        home_team.name = jsonMatch['home_team'];
                        home_team.flag = 'fr';
                        match.home = home_team;
                        // match.away = this.teams[jsonMatch['away_team'] - 1];
                        var away_team = new Team();
                        away_team.id = 0;
                        away_team.name = jsonMatch['away_team'];
                        away_team.flag = 'fr';
                        match.away = away_team;
                        match.group = name_1;
                        _this.matchs.push(match);
                    }
                    break;
                }
            }
        });
    };
    PronoCdmKnockoutComponent.prototype.initValidators = function () {
        if (this.current_knockout == 'round_8') {
            this.form.controls['m5'].setValidators(null);
            this.form.controls['m6'].setValidators(null);
            this.form.controls['m7'].setValidators(null);
            this.form.controls['m8'].setValidators(null);
        }
        else if (this.current_knockout == 'round_4') {
            this.form.controls['m3'].setValidators(null);
            this.form.controls['m4'].setValidators(null);
            this.form.controls['m5'].setValidators(null);
            this.form.controls['m6'].setValidators(null);
            this.form.controls['m7'].setValidators(null);
            this.form.controls['m8'].setValidators(null);
        }
        else if (this.current_knockout == 'round_2') {
            this.form.controls['m3'].setValidators(null);
            this.form.controls['m4'].setValidators(null);
            this.form.controls['m5'].setValidators(null);
            this.form.controls['m6'].setValidators(null);
            this.form.controls['m7'].setValidators(null);
            this.form.controls['m8'].setValidators(null);
        }
    };
    PronoCdmKnockoutComponent.prototype.submit = function () {
        if (this.form.valid) {
            this.form.setValue;
            /*this.http.post(API_URL.concat('prono/cdm/knockout'), this.form.value)
          .toPromise().then(
            d => {
              this.router.navigate(['/cdm/list']);
            }
          );*/
        }
    };
    return PronoCdmKnockoutComponent;
}());
export { PronoCdmKnockoutComponent };
